import React from 'react';
import axios from 'axios';
import { atom, useRecoilState } from 'recoil';
import { UpdateButton } from './atoms/UpdateButton';
import { TodoCard } from './atoms/TodoCard';
import { API_URL } from '../config';

type TodoType = {
  id: string;
  content: string;
  username: string;
  completed: boolean;
};

const todoListState = atom<TodoType[]>({
  key: "todoListState",
  default: []
});

export const TodoList: React.FC = () => {
  const [todoList, setTodoList] = useRecoilState(todoListState);
  const getTodoList = () => {
    console.log("get todolist");
    axios.get(API_URL).then((res) => {
      setTodoList(res.data);
    });
  };

  return (<>
    <h3>Todo List</h3>
    <UpdateButton onClick={ getTodoList } />
    <ul>{ todoList.map((todo) => <li key={ todo.id }><TodoCard todo={ todo } onChange={ getTodoList } /></li>) }</ul>
  </>)
};