import React from 'react';
import axios from 'axios';
import { atom, useRecoilState } from 'recoil';
import { InputContent } from './atoms/InputContent';
import { InputUserName } from './atoms/InputUserName';
import { SendButton } from './atoms/SendButton';
import { API_URL } from '../config';

type TodoType = {
  content: string;
  username: string;
  completed: boolean;
};

const defaultTodo: TodoType = {
  content: "",
  username: "",
  completed: false,
};

const todoState = atom<TodoType>({
  key: 'todoState',
  default: defaultTodo,
});

export const CreateTodo: React.FC = () => {
  const [todo, setTodo] = useRecoilState(todoState);

  const onChangeContent = (event: { target: HTMLInputElement }) => setTodo({ ...todo, content: event.target.value });
  const onChangeUserName = (event: { target: HTMLInputElement }) => setTodo({ ...todo, username: event.target.value });
  const onClickSendButton = () => {
    console.log("post todo");
    console.log(todo);
    axios.post(API_URL, todo);
    setTodo(defaultTodo);
  };

  return (<>
    <h3>Create Todo</h3>
    <InputContent onChange={ onChangeContent } value={ todo.content } />
    <InputUserName onChange={ onChangeUserName } value={ todo.username } />
    <SendButton onClick={ onClickSendButton } />
  </>);
};