import React from 'react';
import axios from 'axios';
import { API_URL } from '../../config';

type PropsType = {
  todo: {
    id: string;
    content: string;
    username: string;
    completed: boolean;
  };
  onChange: () => void;
};

export const TodoCard: React.FC<PropsType> = ({ todo, onChange }) => {
  const patchTodo = (event: {target: HTMLInputElement}) => {
    console.log(`patch todo: ${event.target.checked}`)
    axios.patch(API_URL + `/${todo.id}/` + (event.target.checked ? 'complete' : 'incomplete')).then((res) => {
      console.log(res.data);
      onChange();
    }).catch((err) => {
      console.log(err);
    });
  };
  const deleteTodo = () => {
    console.log(`delete todo`);
    axios.delete(API_URL + `/${todo.id}`).then((res) => {
      console.log(res.data);
      onChange();
    }).catch((err) => {
      console.log(err);
    });
  };
  return (<table>
    <tbody>
      <tr>
        <td>content</td>
        <td>{ todo.content }</td>
      </tr>
      <tr>
        <td>username</td>
        <td>{ todo.username }</td>
      </tr>
      <tr>
        <td>completed</td>
        <td>
          <input type="checkbox" defaultChecked={ todo.completed } onChange={ patchTodo } />
        </td>
      </tr>
      <tr>
        <td></td>
        <td>
          <input type="button" onClick={ deleteTodo } value="delete" />
        </td>
      </tr>
    </tbody>
  </table>);
};